
@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

/*nav*/
@media (min-width: 775px) {
  #Nav_bar{
      position:absolute;
      width:fit-content;
      align-items: center;
      font-size: large;
      z-index:2;
      margin-top:3%;
      
    
  
  }



}


@media (max-width: 775px) {
  #Nav_bar{
      z-index:2;
      position:absolute;
      width:fit-content;
      align-items: center;
      font-size: medium;
      margin-top:6%;
      
      
      
  
  }
  .my_block{
    margin-left:1%;
  }

  
}

















/*body*/
@media (min-width: 768px) {
  .profile{
    
    position:absolute;
    align-items: center;
    padding-top:8%;
  }
  .my_img{
    height:14rem;

  }

  #link-icon{
    
    width:fit-content;
  }
  
  
  
  
}


@media (max-Width:768px) {
  .profile{
    
    position:fixed;
    align-items: center;
    padding-top:20%;
    
    
  }
  .my_img{
    height:14rem;

  }
  
}
